.message {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
        align-items: flex-start;
    margin-top: 16px;
}

.is-loggedin {
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
}

.is-loggedin .message__avatar,
.is-loggedin .message__avatar-stub {
    display: none;
}

.message__avatar,
.message__avatar-stub {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    -ms-flex: 0 0 36px;
        flex: 0 0 36px;
}

.message__avatar-stub {
    color: #fff;
}

.message__content {
    background-color: #fff;
    margin: 0 8px;
    padding: 8px;
    border-radius: 4px;
    min-height: 36px;
}

.message__author {
    font-weight: 600;
}

.message__date {
    -ms-flex-item-align: end;
        align-self: flex-end;
    margin-bottom: 8px;
    color: #908686;
}



